/**
 * loadComponent details()
 * log out the user
 */
export const loadVisualisationItems = (component_id, callback) => async (
  dispatch
) => {
  try {
    // callback(true, false);
    setAuthToken();
    const res = await axios.get(
      `${API_URL}/data/indicator/${component_id}`,
      CONFIG
    );

    dispatch({
      type: VISUALISATION_LOADED,
      payload: res.data,
    });
    callback(true);
  } catch (error) {
    callback(false);
  }
};

/**
 * loadComponent details()
 * log out the user
 */
export const loadVisualisationData = (
  component_id,
  dataRequest,
  callback
) => async (dispatch) => {
  console.log("sent...");

  try {
    // callback(true, false);
    setAuthToken();
    const res = await axios.post(
      `${API_URL}/data/component/${component_id}`,
      { ...dataRequest },
      CONFIG
    );

    // console.log({ data: res.data });

    dispatch({
      type: VISUALISATION_DATA_LOADED,
      payload: res.data,
    });
    callback(true);
  } catch (error) {
    console.log({ ...error });

    callback(false);
  }
};
